<template>
<example-team-news :my-list="list">

</example-team-news>
</template>

<script>
import{reactive,onMounted} from "vue";
import EndPoints from "@/service/EndPoints";
import ExampleTeamNews from "@/components/main/ExampleTeamNews";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "LastNewsBarc",
  components: {ExampleTeamNews},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getNewsBarc()
          .then((response)=>{
            if(response.data.success){
              list.push(response.data.data)
              console.log(response)
            }
          })
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      list,result,
    }
  }
}
</script>

