<template>
<example-super-goal :my-list="showPlayers">

</example-super-goal>
</template>

<script>
import{reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleSuperGoal from "@/components/main/ExampleSuperGoal";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "SuperGoalBarc",
  components: {ExampleSuperGoal},
  setup(){
    const showPlayers=reactive([])
    const sum=onMounted(async ()=>{
     await axios .all([
        axios.get('https://api.footballi.net/api/v2/player/799'),
        axios.get('https://api.footballi.net/api/v2/player/2222247'),
        axios.get('https://api.footballi.net/api/v2/player/1512'),
        axios.get('https://api.footballi.net/api/v2/player/2280904'),
      ])
      .then(axios.spread((...response)=>{
        showPlayers.push(response)
      }))
      .catch((error)=>{
       handleError(error)
      })
    })
    return{
      showPlayers,sum
    }
  }
}
</script>

