<template>
<example-main-attack :my-list="attacker">
  <template #attacker>
    <p class="font-bold text-white font-black w-full">مهاجمان تیم بارسلونا</p>
  </template>
</example-main-attack>
</template>

<script>
import{reactive,onMounted} from 'vue'
import axios from 'axios'
import ExampleMainAttack from "@/components/main/ExampleMainAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "MainAttack",
  components: {ExampleMainAttack},
  setup(){
    const attacker=reactive([])
    const finish=onMounted(async()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/799'),
         axios.get('https://api.footballi.net/api/v2/player/30135'),
         axios.get('https://api.footballi.net/api/v2/player/2222247'),
         axios.get('https://api.footballi.net/api/v2/player/2223224'),
         axios.get('https://api.footballi.net/api/v2/player/2280904'),
      ])
      .then(axios.spread((...response)=>{
        attacker.push(response)
      }))
      .catch((error)=>{
       handleError(error)
      })
    })
    return{
      attacker,finish
    }
  }
}
</script>

