<template>
<example-defender :my-list="showDefender">
  <template #defender>
    <p class="font-bold text-white font-black w-full">مدافعان تیم بارسلونا</p>
  </template>
</example-defender>
</template>

<script>
import{reactive,onMounted} from 'vue'
import axios from 'axios'
import ExampleDefender from "@/components/main/ExampleDefender";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "DefenderBarc",
  components: {ExampleDefender},
  setup(){
    const showDefender=reactive([])
    const result=onMounted(async ()=>{
      await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/855'),
         axios.get('https://api.footballi.net/api/v2/player/1702'),
         axios.get('https://api.footballi.net/api/v2/player/2994'),
         axios.get('https://api.footballi.net/api/v2/player/3058'),
         axios.get('https://api.footballi.net/api/v2/player/5622'),
         axios.get('https://api.footballi.net/api/v2/player/2236457'),
         axios.get('https://api.footballi.net/api/v2/player/2258132'),
         axios.get('https://api.footballi.net/api/v2/player/2317717'),
         axios.get('https://api.footballi.net/api/v2/player/2359622'),
      ])
      .then(axios.spread((...response)=>{
        showDefender.push(response)
      }))
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      showDefender,result
    }
  }
}
</script>

