<template>
<example-attack :my-list="listAttack">
  <template #Midfielder>
    <p class="font-bold text-white font-black w-full">هافبک های تیم بارسلونا</p>
  </template>
</example-attack>
</template>

<script>
import{reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "AttackBarc",
  components: {ExampleAttack},
  setup(){
    const listAttack=reactive([])
    const total=onMounted(async ()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/1512'),
         axios.get('https://api.footballi.net/api/v2/player/22161'),
         axios.get('https://api.footballi.net/api/v2/player/2225860'),
         axios.get('https://api.footballi.net/api/v2/player/2396537'),
         axios.get('https://api.footballi.net/api/v2/player/2444041'),
         axios.get('https://api.footballi.net/api/v2/player/2449400'),

      ])
      .then(axios.spread((...response)=>{
        listAttack.push(response)
      }))
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      listAttack,total
    }
  }
}
</script>

