<template>
<example-transfer :my-list="sum">

</example-transfer>
</template>

<script>
import{reactive,onMounted} from "vue";
import axios from 'axios'
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "TransferTeamBarc",
  components: {ExampleTransfer},
  setup(){
    const sum=reactive([])
    const total=onMounted(async()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/3058'),
         axios.get('https://api.footballi.net/api/v2/player/1512'),
         axios.get('https://api.footballi.net/api/v2/player/3125'),
         axios.get('https://api.footballi.net/api/v2/player/2355'),
         axios.get('https://api.footballi.net/api/v2/player/22161'),
         axios.get('https://api.footballi.net/api/v2/player/2245001'),
         axios.get('https://api.footballi.net/api/v2/player/2229533'),
         axios.get('https://api.footballi.net/api/v2/player/230'),
         axios.get('https://api.footballi.net/api/v2/player/53'),
         axios.get('https://api.footballi.net/api/v2/player/22361'),
      ])
      .then(axios.spread((...response)=>{
        sum.push(response)
      }))
      .catch((error)=>{
       handleError(error)
      })
    })
    return{
      sum,total
    }
  }
}
</script>

