<template>
<example-team-rank :my-list="list">
  <template #rankName>
    <option class="font-bold text-slate-900 text-sm text-start">لیگ برتر اسپانیا</option>
  </template>
</example-team-rank>
</template>

<script>
import{reactive,onMounted} from "vue";
import EndPoints from "@/service/EndPoints";
import exampleTeamRank from "@/components/main/ExampleTeamRank";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "RankBarc",
  components:{exampleTeamRank},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
    EndPoints.getDescriptionBarc()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.standing)
        }
      })
      .catch((error)=>{
       handleError(error)
      })
    })
    return{
      list,
      result,
    }
  }
}
</script>

